"use client"

import Link from "next/link"
import { useState } from "react"
import { Menu, X } from "lucide-react"

export function Header() {
  const [isMenuOpen, setIsMenuOpen] = useState(false)

  const navLinks = [
    { href: "/", label: "Главная" },
    { href: "/blog", label: "Блог" },
    { href: "/pricing", label: "Тарифы" },
    { href: "/faq", label: "FAQ" },
    { href: "/about", label: "О нас" },
  ]

  return (
    <header className="fixed top-0 left-0 right-0 z-50 bg-background/90 backdrop-blur-sm border-b border-border">
      <div className="max-w-7xl mx-auto px-4 py-4">
        <div className="flex items-center justify-between">
          {/* Logo */}
          <Link href="/" className="flex items-center gap-2">
            <div className="w-8 h-8 bg-primary flex items-center justify-center">
              <span className="text-background text-xs">S</span>
            </div>
            <span className="text-primary text-xs md:text-sm">SPACE VPN</span>
          </Link>

          {/* Desktop Navigation */}
          <nav className="hidden md:flex items-center gap-6">
            {navLinks.map((link) => (
              <Link
                key={link.href}
                href={link.href}
                className="text-foreground hover:text-primary transition-colors text-[10px]"
              >
                {link.label}
              </Link>
            ))}
          </nav>

          {/* Auth Buttons */}
          <div className="hidden md:flex items-center gap-4">
            <Link href="/login" className="text-foreground hover:text-primary transition-colors text-[10px]">
              Вход
            </Link>
            <Link
              href="/register"
              className="bg-primary text-primary-foreground px-4 py-2 hover:bg-primary/80 transition-colors text-[10px]"
            >
              Регистрация
            </Link>
          </div>

          {/* Mobile Menu Button */}
          <button className="md:hidden text-foreground" onClick={() => setIsMenuOpen(!isMenuOpen)}>
            {isMenuOpen ? <X size={24} /> : <Menu size={24} />}
          </button>
        </div>

        {/* Mobile Menu */}
        {isMenuOpen && (
          <div className="md:hidden mt-4 pb-4 border-t border-border pt-4">
            <nav className="flex flex-col gap-4">
              {navLinks.map((link) => (
                <Link
                  key={link.href}
                  href={link.href}
                  className="text-foreground hover:text-primary transition-colors text-[10px]"
                  onClick={() => setIsMenuOpen(false)}
                >
                  {link.label}
                </Link>
              ))}
              <div className="flex gap-4 mt-4">
                <Link href="/login" className="text-foreground hover:text-primary text-[10px]">
                  Вход
                </Link>
                <Link href="/register" className="bg-primary text-primary-foreground px-4 py-2 text-[10px]">
                  Регистрация
                </Link>
              </div>
            </nav>
          </div>
        )}
      </div>
    </header>
  )
}
